<style>

select {
    padding-top: 2px;
    padding-bottom : 5px;
}

</style>

<template>
    <div id = 'ResrvView'>
        예약 확인
        <br/> <br/> <br/>
       진료부서 <select id="deptSelect" v-model="deptSelect" v-on:change='requestDoc'>
           <option disabled value="">진료부서</option>
           <option>내과</option>
           <option>외과</option>
           <option>이비인후과</option>
           <option>영상의학과</option>
           <option>가정의학과</option>
           <option>방사선과</option>
           <option>정형외과</option>
           <option>병리과</option>
           <option>응급의학과</option>
           <option>산부인과</option>
           <option>마취통증의학과</option>
           <option>비뇨기과</option>
           <option>신경과</option>
           <option>신경외과</option>
           <option>피부과</option>
           <option>흉부외과</option>
        </select>

        의사 <select v-model="docSelect">
            <option disabled value="">의사</option>
            <option v-for="doctorName in doctorNames" v-bind:key = "doctorName">
                {{doctorName}}</option>

        </select>

        <br /><br /><button v-on:click="submit">검색</button>
        
        <table>
            <td>
                <tr v-for="reservList in reservLists" v-bind:key = "reservList">{{reservList}}</tr>
            </td>
        </table>

    </div>

    
</template>

<script>
export default {
    created () {
        

    },
    data () {
        return {
            deptSelect: '',
            docSelect: '',
            doctorNames: [],
            reservLists : [],

        }
    },
    methods: {
        requestDoc: function() {
            this.$http.post('/api/reservViewDocList', { dept_name : this.deptSelect }).then(response => {
                this.doctorNames = [];
                var i = 0;
                for(i; i < response.data.length; i++) {
                    this.doctorNames.push(response.data[i][0]);
                }
            })
        },
        submit: function() {
            this.$http.post('/api/reservView', { doc_name : this.docSelect, dept_name : this.deptSelect}).then(response => {
                console.log(response.data);
                this.reservLists = response.data;

            })

        }

    }
}
</script>


