<template>
    
    
    <div id="form">
    예약 날짜 <input type="date" v-model="form.date"> <br />
    예약 시간 <input type="time" v-model="form.time" step="1800">  <br />
    의사 번호 <input type="text" v-model="form.doctor_id"> 의사번호찾기<br /> 
    환자 번호 <input type="text" v-model="form.patient_id"> <br />
    진료 부서 <input type="text" v-model="form.dept_id"> <br />
    <button v-on:click="submit">Submit</button>


</div>




</template>

<style>
* {
    margin-bottom : 2px;
}

</style>

<script>

const date = new Date();

console.log(date);


const today = date.getFullYear() + '-' + date.getMonth() + '-01'

console.log(today);


export default {
    created(){
       /* this.$http.get('/api/getSeqReserv').then(response => {
            console.log(response)
            this.reserv_seq = response.data[0];
       })*/
    },
    data () {
        return {
            form: {
                date : today,
                time : '09:00',
                doctor_id : '',
                patient_id : '',
                dept_id : '',
            },
            reserv_seq : '',

        }
    },
    methods: {
        submit: function () {
            this.$http.post('/api/reservation', { reserv_id : this.reserv_seq, doctor_id : this.form.doctor_id, 
                                            patient_id : this.form.patient_id, dept_id : this.form.dept_id, 
                                            date:this.form.date, time: this.form.time }).then(response => {
                console.log(response)
                this.result = response.date
                this.no = response.data.no
                /*
                if(response === 'success') {     
                        this.$http.get('/api/reservNextval').then(response => {
                        console.log(response)
                    }); 
                }*/
            }).catch((ex) => {
                console.warn("error occured, ex")
            });

        }
    }

}
</script>


